.orbit-spinner, .orbit-spinner * {
  box-sizing: border-box;
}

.orbit-spinner {
  height: 55px;
  width: 55px;
  perspective: 800px;
  border-radius: 50%;
  display: block;
}

.background-spinner {
  z-index: 9;
  width: 100%;
  height: 100%;
  background-color: rgba(12, 11, 11, .6);
  justify-content: center;
  align-items: center;
  display: flex;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.background-spinner.is-hidden {
  display: none;
}

.orbit-spinner .orbit {
  box-sizing: border-box;
  width: 100%;
  height: 100%;
  border-radius: 50%;
  position: absolute;
}

.orbit-spinner .orbit:nth-child(1) {
  border-bottom: 3px solid #ff1d5e;
  animation: orbit-spinner-orbit-one-animation 1.2s linear infinite;
  top: 0%;
  left: 0%;
}

.orbit-spinner .orbit:nth-child(2) {
  border-right: 3px solid #ff1d5e;
  animation: orbit-spinner-orbit-two-animation 1.2s linear infinite;
  top: 0%;
  right: 0%;
}

.orbit-spinner .orbit:nth-child(3) {
  border-top: 3px solid #ff1d5e;
  animation: orbit-spinner-orbit-three-animation 1.2s linear infinite;
  bottom: 0%;
  right: 0%;
}

@keyframes orbit-spinner-orbit-one-animation {
  0% {
    transform: rotateX(35deg)rotateY(-45deg)rotateZ(0deg);
  }

  100% {
    transform: rotateX(35deg)rotateY(-45deg)rotateZ(360deg);
  }
}

@keyframes orbit-spinner-orbit-two-animation {
  0% {
    transform: rotateX(50deg)rotateY(10deg)rotateZ(0deg);
  }

  100% {
    transform: rotateX(50deg)rotateY(10deg)rotateZ(360deg);
  }
}

@keyframes orbit-spinner-orbit-three-animation {
  0% {
    transform: rotateX(35deg)rotateY(55deg)rotateZ(0deg);
  }

  100% {
    transform: rotateX(35deg)rotateY(55deg)rotateZ(360deg);
  }
}

